@use "@miq/utils/src/style/variables" as vars;

.document-update-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.document-update-form {
    .field {
        margin-bottom: 1rem;

        label {
            display: block;
            padding-bottom: 0.5rem;
        }

        .textareax {
            .textareax-mirror,
            textarea {
                padding: vars.$input-padding;
            }

            textarea {
                border-radius: vars.$input-border-radius;
                border-color: vars.$input-border-color;
            }
        }
    }
}
